import React, {useState,useEffect} from 'react'
import { StyleSheet, Text, View } from 'react-native'
import {Button,Input} from 'react-native-elements'

const Home = () => {
    useEffect(() => {
        if (input === ""){
            setInput(5)
        }
    },[])
    const [money,setMoney] = useState(localStorage.getItem("money"))
    const [input,setInput] = useState('');
    const append = () => {
        setMoney(parseInt(money) + parseInt(input));
        localStorage.setItem("money",parseInt(money)+parseInt(input))
    }
    const clearJar = () => {
        localStorage.setItem("money",0)
        setMoney(localStorage.getItem("money"))
     }
    return (
        <View>
            <Text style={styles.title} >Every time you swear , you deposit money here. At the end of every month you see how much money got deposited and then try to lessen that every month.</Text>
            <Text style={styles.title} >Amount in swear jar : {money} $</Text>
            <Input    keyboardType='numeric'
placeholder="Amount to Deposit in swear jar" value={input} onChangeText={(text) => setInput(text)}/>
            <Button title="Deposit money into swear jar" onPress={append} style={{paddingBottom:20}}/>
            <Button title="Clear swear jar" onPress={clearJar}/>

        </View>
    )
}

export default Home

const styles = StyleSheet.create({
    title:{
        fontWeight:"bold",
        fontSize:30,
        alignItems:"center",
        justifyContent:"center",
        padding:15,
    }
})
